<template>
  <div class="typeDetails">
    <header>
      <van-nav-bar
        :title="title"
        left-text="返回"
        left-arrow
        @click-left="back"
      />
    </header>
    <div class="container">
      <van-list
        v-if="list"
        v-model="loading_2"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell v-for="item in list" :key="item._id">
          <BookItemTypeTwo :item="item" :loading="loading" />
        </van-cell>
      </van-list>
    </div>
    <BackToTop />
  </div>
</template>
<script>
import BookItemTypeTwo from "@/components/BookItemTypeTwo.vue";
import BackToTop from "@/components/BackToTop.vue";
export default {
  components: {
    BackToTop,
    BookItemTypeTwo,
  },
  data() {
    return {
      // 获取route数据
      books: this.$route.query.books
        ? JSON.parse(this.$route.query.books)
        : null,
      loading: this.$route.query.loading
        ? JSON.parse(this.$route.query.loading)
        : null,
      title: this.$route.query.title ? this.$route.query.title : null,
      // 懒加载
      loading_2: false,
      finished: false,
      list: this.$route.query.books
        ? JSON.parse(this.$route.query.books).slice(0, 10)
        : null,
      num: 1,
    };
  },
  created() {
    // 回到顶部
    document.documentElement.scrollTop = 0;
    // console.log(this.$route.query.typeName);
    // 判断是否有这个list和books然后再决定是否查询数据
    if (!this.list || !this.books) {
      this.searchType();
    }
  },
  //新加
  // activated() {
  //   this.searchType();
  // },
  methods: {
    back() {
      console.log("back");
      this.$router.back();
    },
    onLoad() {
      setTimeout(() => {
        this.num =
          this.num > this.books.length / 10
            ? this.books.length / 10
            : ++this.num;
        this.list = [...this.books.slice(0, this.num * 10)];

        // 加载状态结束
        this.loading_2 = false;

        // 数据全部加载完成
        if (this.list.length >= this.books.length) {
          this.finished = true;
        }
      }, 1000);
    },
    searchType() {
      this.$root.isShowLoading = true;
      this.axios(
        "/category-info?gender=" +
          this.$route.query.sex +
          "&type=hot&major=" +
          this.$route.query.typeName +
          "&minor=" +
          this.$route.query.minName
      )
        .then((res) => {
          // console.log(res.data.books.slice(0,40));
          this.books = res.data.books;
          this.list = res.data.books.slice(0, 10);
        })
        .finally(() => {
          this.loading = false;
          this.$root.isShowLoading = false;
        });
    },
  },
};
</script>

<style lang="less">
.typeDetails {
  margin-bottom: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100vw;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
    z-index: 2;
  }
  .container {
    padding: 50px 3px 0;
    box-sizing: border-box;
    .van-cell {
      padding: 0;
    }
  }
}
</style>